//system
import React, { Component } from 'react';
import logo from './logo.svg';
import { ParallaxProvider, Parallax } from 'react-scroll-parallax';
//css
import './App.css';
//components
import Navigation from './Components/Navigation/Navigation';
import USP from './Components/USP/usp';
import Partners from './Components/Partners/Partners';
import NewProperties from './Components/NewProperties/NewProperties';
import Information from './Components/Information/Information';
import BestPropertyPrice from './Components/BestPropertyPrice/BestPropertyPrice';
import Reviews from './Components/Reviews/Review';
import GetStarted from './Components/GetStarted/GetStarted';
import Footer from './Components/Footer/Footer';

const ParallaxImage = ({ image, min, max, children }) => (
    <div className="parallax-container">
        <Parallax offsetYMin={min} offsetYMax={max} slowerScrollRate>
            <div
                className="parallax-image"
                style={{ backgroundImage: `url(${image})` }}
            />
        </Parallax>
        <div className="parallax-children">{children}</div>
    </div>
);

class App extends Component {
  constructor() {
        super();
        this.state = {
          todos: [
            'Buying and selling a home marks the start of a new chapter in life. At Property Cat we believe in a dramatically simplified experience, delivering confidence and peace of mind at every step. If you’re selling, sell your home to us to eliminate the hassles of showings and months of uncertainty. If you’re buying, we make it incredibly easy to tour hundreds of Opendoor homes so you can find the perfect one.',
            'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
            'Buying and selling a home marks the start of a new chapter in life. At Property Cat we believe in a dramatically simplified experience, delivering confidence and peace of mind at every step. If you’re selling, sell your home to us to eliminate the hassles of showings and months of uncertainty. If you’re buying, we make it incredibly easy to tour hundreds of Opendoor homes so you can find the perfect one.',
            'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'],
          pics: [
            'https://static.euronews.com/articles/stories/03/21/73/66/880x495_cmsv2_298e3b01-877d-57e3-9ce0-0542084c5af4-3217366.jpg',
            'https://everyday-c80f.kxcdn.com/wp-content/uploads/2017/03/76-Inspirational-picture-quotes-and-motivational-images.jpg',
            'https://travel.usnews.com/static-travel/images/destinations/16/grand_canyon_main_2014_-_shutterstock-kojihirano.jpg',
            'https://a.wayin.com/images/5355/4d0cf021-7226-42c0-8b50-f5a6579e5f90/nicola-tolin-422795-unsplash.jpg'],
          picsDescription: [
            'Manhattan, New York',
            'Seahawk St, Las Vegas',
            'Springfield, Colorado',
            'Compton, Los Angeles'],
          currentPage: 1,
          elementPerPage: 1
        };
        this.handleClick = this.handleClick.bind(this);
      }

      handleClick(event) {
        this.setState({
          currentPage: Number(event.target.id)
        });
      }
 

  render() {
    const { todos, pics, picsDescription, currentPage, elementPerPage } = this.state;
// Logic for displaying current elements
    const indexOfLastElement = currentPage * elementPerPage;
    const indexOfFirstElement = indexOfLastElement - elementPerPage;
    const currentTodos = todos.slice(indexOfFirstElement, indexOfLastElement);
    const currentPics = pics.slice(indexOfFirstElement, indexOfLastElement);
    const currentPicsDescription = picsDescription.slice(indexOfFirstElement, indexOfLastElement);

    const renderTodos = currentTodos.map((todo, index) => {
      return <p key={index}>{todo}</p>;
    });

    const renderPics = currentPics.map((pic) => {
      return <img className='information-image' src={pic}/>;
    });

     const renderPicsDescription = currentPicsDescription.map((description) => {
      return <div className='information-image-text'>{description}</div>;
    });
    // Logic for displaying page numbers
    const pageNumbers = [];
      for (let i = 1; i <= Math.ceil(todos.length / elementPerPage); i++) {
        pageNumbers.push(i);
      }

    const renderPageNumbers = pageNumbers.map(number => {
      return (
        <p
          className={(this.state.currentPage === number ? 'active ' : '') + 'controls'}
          key={number}
          id={number}
          onClick={this.handleClick}
        >
          {number}
        </p>
      );
    });
   
    return (
      <div className='main-block'>
        <Navigation/>
        <ParallaxProvider className='parallax-provider'>
          <ParallaxImage
              className='parallax-image'
              min={'-20%'}
              max={'20%'}
              image="https://pp.userapi.com/c851124/v851124224/8b097/FddeB1-w9rY.jpg"
          >
              <h1 className='parallax-text'>Sell todAY WITH PROPERTY CATS YOUR HOME AND ONLY CHARGE A FEE IF IS SELL OVER  THE ASKING PRICE</h1>
          </ParallaxImage>
        </ParallaxProvider>
        <USP/>
        <Partners/>
        <NewProperties/>
        <Information renderTodos={renderTodos} renderPics={renderPics} renderPicsDescription={renderPicsDescription} renderPageNumbers={renderPageNumbers} currentPage={currentPage}/>
        <BestPropertyPrice/>
        <Reviews/>
        <GetStarted/>
        <Footer/>
      </div>
    );
  }
}

export default App;
