import React from 'react'
import './GetStarted.css'
import Image1 from '../../Images/GetStarted/terrah-holly-16329-unsplash.jpg';
import { ParallaxProvider, Parallax } from 'react-scroll-parallax';


export default () => {
			const ParallaxImage = ({ image, min, max, children }) => (
		    <div className="getstarted-parallax-container">
		        <Parallax offsetYMin={min} offsetYMax={max} slowerScrollRate>
		            <div
		                className="getstarted-parallax-image"
		                style={{backgroundImage: `url(${image})`}}
		            />
		        </Parallax>
		        <div className="getstarted-parallax-children">{children}</div>
		    </div>
			);
	    return (
	    	<ParallaxProvider className='getstarted-parallax-provider'>
          <ParallaxImage
              min={'-20%'}
              max={'20%'}
              image={Image1}
          >
              <h1 className='getstarted-parallax-text'>We've dramatically simplified the buying and selling process</h1>
              <button type="button" className="getstarted-button">Get Start</button>
          </ParallaxImage>
        </ParallaxProvider>
	    	)
}
